	data() {
		return {
			scrollLeft: 0,
			isClickChange: false,
			currentTab: 0,
			menuTabs: [{
				name: '买进'
			}, {
				name: '卖出'
			}
			],
			// swiperDateList:[['买进','买进2'],['卖出','卖出2']]
			swiperDateList: [
				[
					{
					    img: '../../static/1.jpg',
					    name: '狐狸叔叔',
					    message: '今晚去吃饭吗?',
					    time: '19:08',
					},
					{
					    img: '../../static/2.jpg',
					    name: '老虎爸爸',
					    message: '黑发不知勤学早，白首方悔读书迟。 —— 颜真卿《劝学诗》',
					    time: '02:08',
					}
				],
				[
					{
					    img: '../../static/3.jpg',
					    name: '偶遇妹子',
					    message: '忽如一夜春风来，千树万树梨花开。 —— 岑参《白雪歌送武判官归京》',
					    time: '02:08',
					},
					{
					    img: '../../static/4.jpg',
					    name: '男神',
					    message: '寂寞空庭春欲晚，梨花满地不开门。 —— 刘方平《春怨》',
					    time: '23:08',
					}
				]
			]
		}
	},
	onLoad() {
		//初始化数据
		this.getDateIncome();
		this.getDatePay();
		},
	methods: {
		swichMenu: async function(current) { //点击其中一个 menu
				if (this.currentTab == current) {
					return false;
				} else {
					this.currentTab = current;
					this.setScrollLeft(current);
				}
			},
			swiperChange: async function(e) {
					let index = e.target.current;
					this.setScrollLeft(index);
					this.currentTab = index; 
				},
				setScrollLeft: async function(tabIndex) {
						let leftWidthSum = 0;
						for (var i = 0; i <= tabIndex; i++) {
							let nowElement = await this.getWidth('tabNum' + i);
							leftWidthSum = leftWidthSum + nowElement.width;
						}
						let winWidth = uni.getSystemInfoSync().windowWidth;
						this.scrollLeft = leftWidthSum > winWidth ? (leftWidthSum - winWidth) : 0
					},
					getWidth: function(id) { //得到元素的宽高
						return new Promise((res, rej) => {
							uni.createSelectorQuery().select("#" + id).fields({
								size: true,
								scrollOffset: true
							}, (data) => {
								res(data);
							}).exec();
						})
					},
					loadMore: function(tabIndex) {
						console.log('正在加载更多数据。。。')
						this.getDateList(tabIndex);
					},
					getDateIncome() {
						var token = (uni.getStorageSync("user_info")).token;
						console.log(token)
						var type = 1;
						uni.request({
							url:'http://bus.liebianzhe.com/api/user/my_shop',
							data:{
								token:token,
								type:type
							},
							success: (res) => {
								console.log(res)
							}
						})
					},
					getDatePay(){
						// var token = (uni.getStorageSync('user_info')).token;
						var token=(uni.getStorageSync("user_info")).token;
						var type = 2;
						uni.request({
							url:'http://bus.liebianzhe.com/api/user/my_shop',
							data:{
								token:token,
								type:type
							},
							success: (res) => {
								console.log(res)
							}
						})
					}

			}

	}
